import '@gdk/base/dist/fonts/GEICORoobert-BoldItalic.woff';
import '@gdk/base/dist/fonts/GEICORoobert-BoldItalic.woff2';

import '@gdk/styles';

import '../../sass-kit/geico-design-kit';

import appState from '@gdk/app-state';
