import { GdkTextareaCountdown as TextareaCountdown } from '@gdk/textarea-countdown';
import { GdkDatePicker as DatePicker } from '@gdk/datepicker';
import { GdkAddressAutoComplete as AddressAutoComplete } from '@gdk/address-auto-complete';
import { GdkTabs as Tabs } from '@gdk/tabs';
import { GdkProgressIndicator as ProgressIndicator } from '@gdk/progress-indicator';
import { GdkViewMoreLess as ViewMoreLess } from '@gdk/view-more-less';
import { GdkToTopArrow as ToTopArrow } from '@gdk/to-top-arrow';
import ValidateForm from '@gdk/validate-form';
import { GdkInPageNavigation as InPageNavigation } from '@gdk/in-page-navigation';
import { GdkTimeline as Timeline } from '@gdk/timeline';
import { GdkAlert as Alert } from '@gdk/alert';
import { GdkAccordion as Accordion } from '@gdk/accordion';
import { GdkMoreInfoButton as MoreInfoButton } from '@gdk/more-info-button';
import { GdkDotNavigation as DotNavigation } from '@gdk/dot-navigation';
import { GdkCoverageGraph as CoverageGraph } from '@gdk/coverage-graph';
import Portfolio from '@gdk/portfolio';
import PayPlans from '@gdk/payment-plans';
import { GdkConsolidatedSummary as ConsolidatedSummary } from '@gdk/consolidated-summary';
import { GdkEditComponent as EditComponent } from '@gdk/edit-component';
import { GdkDockedMessage as DockedMessage } from '@gdk/docked-message';
import { GdkSegmentedControl as SegmentedControl } from '@gdk/segmented-control';
import { GdkMultipleSelectBox as MultipleSelectBox } from '@gdk/multiple-select-box';
import { GdkCardSelections as CardSelections } from '@gdk/card-selections';
import { GdkTimeInput as TimeInput } from '@gdk/time-input';
import { GdkNavigationalBox as NavigationalBox } from '@gdk/navigational-box';
import { GdkPasswordMeter as PasswordMeter } from '@gdk/password-meter';
import { GdkCreditCard as CreditCard } from '@gdk/credit-card';
import { GdkCurrencyInput as CurrencyInput } from '@gdk/currency-input';
import MakePayment from '@gdk/make-payment';
import { GdkVIN as VIN} from '@gdk/vin';
import { GdkZipCode as ZipCode } from "@gdk/zip-code";
import { GdkExpandableForm as ExpandableForm} from "@gdk/expandable-form";
import { GdkInstructionalOverlay as InstructionalOverlay } from '@gdk/instructional-overlay';
import { GdkQuoteRecall as QuoteRecall } from '@gdk/quote-recall';
import { GdkSummaryNavigator as SummaryNavigator } from '@gdk/summary-navigator';
import { GdkImageSelections as ImageSelections } from '@gdk/image-selections';
import { GdkSocialSecurityNumber as SocialSecurityNumber } from '@gdk/social-security-number';
import { GdkCheckpoint as Checkpoint } from '@gdk/checkpoint';
import { GdkToasts as Toasts } from '@gdk/toasts';
import { GdkExpandableRadios as ExpandableRadios } from "@gdk/expandable-radios";
import { GdkCarousel as Carousel } from "@gdk/carousel";
import { GdkProgressBar as ProgressBar } from "@gdk/progress-bar";
import { GdkDateOfBirth as DateOfBirth } from "@gdk/date-of-birth";
import { GdkPillFilter as PillFilter } from "@gdk/pill-filter";
import { GdkDarkModeSwitch as DarkModeSwitch } from "@gdk/dark-mode-switch";
import { GdkShoppingBar as ShoppingBar } from "@gdk/shopping-bar";
import { GdkFein } from '@gdk/fein';

let GDK = GDK || {};
import version from './version'

function init() {

	let about = {
		version: version.version
	};

	let ProtoGDK = {
		about: about,
		Navigation,
		Modal,
		Tooltip,
		Loader,
		ButtonSwitch,
		TextareaCountdown,
		DatePicker,
		AddressAutoComplete,
		Tabs,
		ViewMoreLess,
		ValidateForm,
		InPageNavigation,
		Timeline,
		Accordion,
		ToTopArrow,
		Alert,
		MoreInfoButton,
		DotNavigation,
		CoverageGraph,
		ConsolidatedSummary,
		Footer,
		PayPlans,
		MultipleSelectBox,
		Portfolio,
		EditComponent,
		DockedMessage,
		SegmentedControl,
		CardSelections,
		TimeInput,
		NavigationalBox,
		components,
		PasswordMeter,
		CurrencyInput,
		CreditCard,
		MakePayment,
		VIN,
		ZipCode,
		ProgressIndicator,
		ExpandableForm,
		InstructionalOverlay,
		QuoteRecall,
		SummaryNavigator,
		ImageSelections,
		Checkpoint,
		SocialSecurityNumber,
		Toasts,
		ExpandableRadios,
		Carousel,
		ProgressBar,
		DateOfBirth,
		PillFilter,
		DarkModeSwitch,
		ShoppingBar,
		GdkFein
	};

	for (let property in ProtoGDK) {
		if (!GDK.hasOwnProperty(property)) {
			GDK[property] = ProtoGDK[property];
		}
	}

	window.addEventListener('resize', () => {
		handleResize();
	});

	handleResize();

}


function handleResize() {
	appState.windowSize = { width: window.innerWidth || document.documentElement.clientWidth, height: window.innerHeight || document.documentElement.clientHeight };
}

init();
window.GDK = GDK;
export { GDK };
